@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
html {
  height: 100%;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/react-weather-app/static/media/background.03d01323.jpg);
  background-attachment: fixed;
  background-color: #a6d5df;
  background-size: 100%;
  background-position: bottom;
  background-repeat: no-repeat;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

::-webkit-scrollbar { /* webkit */
  width: 0;
  height: 0;
}

.current-weather {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 30px;
    border-radius: 10px;
    min-width: 300px;
    max-width: 50%;
    cursor: default;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.current-weather-location {
    display: flex;
    align-items: center;
    text-align: center;
    color: #ffffff;
    text-shadow: 1px 1px 2px #162635;
    font-size: 32px;
}

.current-weather-info {
    display: flex;
    align-items: center;
    margin-top: 7px;
}

.current-weather-info-text {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
    color: #ffffff;
    text-shadow: 1px 1px 2px #162635;
    font-size: 26px;
}

.current-weather-info-temperature {
    font-weight: bold;
    font-size: 32px;
    margin-right: 10px;
}

.current-weather-info-description {
    display: flex;
    flex-shrink: 1;
}

.current-weather-info-icon {
    margin-right: 5px;
}



.daily-forecast {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
    padding: 0 80px;
}

.daily-forecast-date {
    font-size: 24px;
    color: #162635;
}

.daily-forecast-dayofweek {
    margin-top: 5px;
    font-size: 18px;
    color: #162635;
}

.daily-forecast-temperature {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 10px;
    font-size: 26px;
    font-weight: bold;
}

.daily-forecast-temperature-max {
    color: #162635;
}

.daily-forecast-temperature-min {
    color: #243d51;
}

.daily-forecast-weather {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around;
    font-size: 18px;
    margin-top: 10px;
}

.daily-forecast-chance-of-rain {
    display: flex;
    align-items: center;
}

.daily-forecast-chance-of-rain-icon {
    padding-bottom: 5px;
}

.daily-forecast-wind-speed {
    display: flex;
    align-items: center;
}

.daily-forecast-wind-speed-icon {
    margin: 0 5px;
}

.hourly-forecast-date {
    margin: 5px 0;
    font-size: 18px;
}

.hourly-forecast-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 4px 7px;
    font-size: 14px;
    color: #162635;
}

.hourly-forecast-item-icon {
    width: 36px;
    height: 36px;
}

.hourly-forecast-item-temperature {
    font-size: 18px;
}


.loader-container{
    position: absolute;
    width: 100%;
    height: 100%;
}
.loader {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.lds-ring {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}
.lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 100px;
    height: 100px;
    border: 8px solid #fff;
    border-radius: 50%;
    -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #fff transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
    -webkit-animation-delay: -0.45s;
            animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
    -webkit-animation-delay: -0.15s;
            animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
@keyframes lds-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

